@import url(https://fonts.googleapis.com/css?family=Sen:400,700&display=swap);
*{margin:0;padding:0}*,::before,::after{box-sizing:inherit}html{box-sizing:border-box;font-size:62.5%}body{font-family:'Architects Daughter', cursive;font-size:1.5rem;background-image:url(/Todo-app/static/media/background.78ee9c8a.png)}

.todo{margin:5rem 9rem;display:flex;align-items:center;flex-direction:column}.todo__header{color:seashell;transform:rotate(2deg);padding:0.2rem 1.2rem;border-radius:20% 5% 20% 5%/5% 20% 25% 20%;background-color:#fdcb6e;font-size:3rem}.btn{cursor:pointer;border:none;background-color:#5566fd;margin:1rem;padding:0.7rem 1.2rem;border-radius:20% 5% 20% 5%/5% 20% 25% 20%;color:#fff}.btn:hover{background-color:#5461d6;transform:rotate(-2deg)}.done{text-decoration:line-through}.footer{margin-top:3rem;color:white}

.todoForm{padding:2rem;color:#fff}.todoForm__label{margin-right:2rem;font-size:2rem}.todoForm__input{border:none;padding:0.5rem;border-radius:2px}

.item{color:#fff;font-size:2rem;list-style:none}.checkbox{border:none;margin:1rem;padding:2rem}

